*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-fonts: "Montserrat", sans-serif;
  --secondary-fonts: "Seymour One", sans-serif;
  --numder-fonts: "Roboto", sans-serif;
  --main-text-color: #fcfcfc;
  --secondary-text-color: #131313;
  --white-text-color: #fff;
  --light-text-time-color: #ababab;
  --defoult-nav-text-color: #f8d24e;
  --hover-nav-text-color: #0a8817;
  --focus-nav-tex-color: #fcfcfc;
  --disable-nav-text-color: #ababab;
  --defoult-border: 2px solid #f8d24e;
  --hover-focus-border: 2px solid #0a8817;
  --disable-border: 2px solid #ababab;
  --defoult-btn-bgc: #f8d24e;
  --hover-focus-btn-bgc: #0a8817;
  --disable-btn-bgc: #ababab;
  --footer-link-text-color: #7a7a7a;
  --address-link-text-color: #d6936d;
  --main-bgc-color: #84a178;
  --white-bgc-color: #fff;
  --secondary-bgc-color: #f8d24e;
  --ice-cream-bgc: #aec6a5;
  --coffee-bgc: #efd478;
  --milk-drinks-bgc: #d6936d;
  --ice-cream-border: 2px solid #aec6a5;
  --coffee-border: 2px solid #efd478;
  --milk-drinks-border: 2px solid #d6936d;
  --cards-filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, .1));
  --main-box-shadow: 0px 8px 30px rgba(212, 20, 67, .1);
  --active-dots-bgc: #84a178;
  --inactive-dots-bgc: #d9d9d9;
  --main-modal-border: 2px solid #f8d24e;
  --ingrediends-border: 2px solid #9cbe8f;
  --ingrediends-bgc: #9cbe8f;
  --ingrediends-bow-shadow: 0px 6px 20px rgba(10, 136, 23, .25);
  --input-form-bgc: rgba(248, 210, 78, .1);
  --form-border: 1px solid #84a178;
  --animation-fast: 250ms cubic-bezier(.4, 0, .2, 1);
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  display: none;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

html {
  scroll-behavior: smooth;
}

body {
